.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.nav-bar {
  display:flex;
  align-items: center;
  justify-content: space-evenly;
  background: #282c34;
  text-decoration: none;
  padding: 1rem;
}

.nav-link {
  color: #61dafb;
  text-decoration: none;  
}

.nav-link:hover {
  color:white;
}

.active-link {
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
